
#about-wrapper {
    margin: 50px 100px 70px 100px;
}

.about-container {
    display: flex;
    /* align-items: center; */
    margin-bottom: 70px;
    
}

.about-right > h1 {
    margin-top: 0px;
    color: #B0B3B8;
}

.profile-img {
    height: 250px;
    width: 250px;
    border-radius: 50%;
    margin-right: 35px;
}

.about-description {
    /* color: #E4E6EB; */
    color: #B0B3B8;

    font-size: 20px;
    line-height: 32px;
}

.contact-icon {
    color: #B0B3B8;
    margin: 10px;
    font-size: 40px;
}

.tech-container {
    display: flex;
    margin-top: 50px;
    flex-direction: column;
}

.tech-header {
    color: #B0B3B8;
}

.technologies {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.tech__icon {
    margin:25px;
    font-size: 80px;
}